<template>
  <div class="page">
    <div class="orderGroup">
      <ol>
        <li class="active">全部</li>
        <li>待付款</li>
        <li>待发货</li>
        <li>待收货</li>
        <li>退换/售后</li>
      </ol>
      <ul>
        <orderGood
          v-for="(item,index) in 3"
          :key="index"
      />
      </ul>
    </div>
    <!-- 底部导航栏 -->
    <tabBar :Tabbar="2" />
  </div>
</template>

<script>
import orderGood from '../components/good/OrderGood'
import tabBar from '../components/TabBar'
export default {
  data() {
    return {
    };
  },
  components: {
    orderGood,
    tabBar,
  }
};
</script>

<style lang="less" scoped>
.page {
  min-height: 100%;
  background: #f3f3f3;
  .orderGroup {
    padding-bottom: 64px;
    background: #f3f3f3;
    ol {
      display: inline-block;
      margin-left: .01rem;
      border-bottom: solid 1px #e5e5e5;
      li {
        float: left;
        width: 1.97rem;
        height: 1.17rem;
        margin-top: .24rem;
        border-radius: 5px 5px 0px 0px;
        border-right: solid 1px #e5e5e5;
        background: #ffffff;
        color: #444444;
        font-size: 15px;
        line-height: 1.17rem;
        text-align: center;
      }
      li.active {
        background: #0055ff;
        color: #ffffff;
      }
    }
  }
}
</style>




